<template>
  <div class="container-fluid bg-dark">
    <div class="row py-5">
      <div class="profile-container col-12 col-md-3 mx-auto py-4">
        <router-link
          to="/profile"
          class="btn-back btn btn-success my-2 d-block w-25"
          >&#8626; Trang cá nhân</router-link
        >
        <h3 class="my-4 text-center">
          <i class="user-icon bi bi-person-circle"></i>
          Cập nhật thông tin tài khoản
        </h3>
        <p class="text-danger" style="font-size: 0.8rem">
          <b>&rArr; Lưu ý:</b>
          Các trường dữ liệu không được nhập hoặc bỏ trống sẽ được giữ
        </p>
        <!-- @submit.prevent="handleSubmit" -->
        <form @submit.prevent="handleSubmit">
          <div class="form-floating mb-3">
            <input
              type="text"
              class="form-control"
              name="fullName"
              id="fullName"
              :value="userForm.fullName"
              :class="{
                'is-invalid': isSubmitted && $v.userForm.fullName.$error,
              }"
            />
            <label for="fullName">Họ tên</label>
            <div
              v-if="isSubmitted && $v.userForm.fullName.$error"
              class="invalid-feedback"
            >
              <span v-if="!$v.userForm.fullName.required"
                >Họ tên không được bỏ trống</span
              >
            </div>
          </div>
          <div class="form-floating mb-3">
            <input
              type="email"
              class="form-control"
              name="email"
              id="email"
              v-model="userForm.email"
              :class="{ 'is-invalid': isSubmitted && $v.userForm.email.$error }"
            />
            <label for="email">Email</label>
            <div
              v-if="isSubmitted && $v.userForm.email.$error"
              class="invalid-feedback"
            >
              <span v-if="!$v.userForm.email.required"
                >Email không được bỏ trống</span
              >
              <span v-if="!$v.userForm.email.email"
                >Vui lòng nhập đúng định dạng email</span
              >
            </div>
          </div>
          <div class="form-floating mb-3">
            <input
              type="text"
              class="form-control"
              name="phone"
              id="phone"
              v-model="userForm.phone"
              :class="{
                'is-invalid': isSubmitted && $v.userForm.required.$error,
              }"
            />
            <label for="phone">Số điện thoại</label>
            <div
              v-if="isSubmitted && $v.userForm.phone.$error"
              class="invalid-feedback"
            >
              <span v-if="!$v.userForm.password.required"
                >Số điện thoại không được bỏ trống</span
              >
            </div>
          </div>
          <div class="form-floating mb-3">
            <input
              type="text"
              class="form-control"
              name="address"
              id="address"
              v-model="userForm.address"
              :class="{
                'is-invalid': isSubmitted && $v.userForm.required.$error,
              }"
            />
            <label for="address">Địa chỉ</label>
            <div
              v-if="isSubmitted && $v.userForm.address.$error"
              class="invalid-feedback"
            >
              <span v-if="!$v.userForm.address.required"
                >Địa chỉ không được bỏ trống</span
              >
            </div>
          </div>
          <p class="message text-success"></p>
          <div class="form-group mt-3">
            <button
              class="btn-update btn btn-primary btn-block w-100 text-center"
            >
              Cập nhật
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { required, email } from "vuelidate/lib/validators";
import { mapMutations, mapActions, mapGetters } from "vuex";
export default {
  name: "Update",
  data() {
    return {
      userForm: {
        fullName: this.$store.state.user.fullName,
        email: this.$store.state.user.email,
        phone: this.$store.state.user.phone,
        address: this.$store.state.user.address,
      },
      isSubmitted: false,
    };
  },
  validations: {
    userForm: {
      fullName: {
        required,
      },
      email: {
        required,
        email,
      },
      phone: {
        required,
      },
      address: {
        required,
      },
    },
  },
  created: function () {
    // this.getInfoAccount();
  },
  computed: {
    ...mapGetters(["user"]),
  },
  methods: {
    ...mapMutations(["getInfoAcount"]),
    ...mapActions(["getInfoAccount", "updateProfile"]),
    handleSubmit() {
      this.isSubmitted = true;
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }
      this.updateProfile();
    },
    // async update() {

    // console.log(fullName)
    // console.log(email)
    // console.log(phone)
    // console.log(address)
    // await this.updateProfile(fullName, email, phone, address);
    // },
  },
};
</script>

